.charaHeader {
    display: inline;
}

.charaDisplay {
    width: 35%;
    align-items: left;
}

.charaInfo {
    width: 65%;
    display: block;
    justify-content: left;
}

.charaBody {
    display: inline-flex;
    justify-content: center;
}

.brighteName, .lalehName, .lydiName,
.nakoaName, .rinName, .robynName, 
.suminaName {
    font-size: 210%;
    font-family: 'Playfair Display', serif;
    display: flex;
    justify-content: center;
    background-color: #d6d6d6;
    margin-top: 25px;
}

.brighteName, .brighteDetailTitle {
    color: #676aa6;
}

.lalehName, .lalehDetailTitle {
    color: #a4814b;
}

.lydiName, .lydiDetailTitle {
    color: #92c6e0;
}

.nakoaName, .nakoaDetailTitle {
    color: #b55038;
}

.rinName, .rinDetailTitle {
    color: #275435;
}

.robynName, .robynDetailTitle {
    color: #5b7086;
}

.suminaName, .suminaDetailTitle {
    color: #cc3d4b;
}

.titleInfo {
    display: inline-block;
    flex-direction: column;
    text-align: left;
    margin-left: 10px;
    line-height: 0.2;  
}

.pronounciation {
    display: flex;
    justify-content: center;

    color: #939494;
    font-size: 14px;
    font-style: italic;
}

.cardImg {
    width: 175px;
    height: 255px;
    border: 1px solid grey;
    border-radius: 5px;

    margin: 50px 5px -10px 0px;
}

.fullbodyImg {
    width: 454px;
    height: 650px;
    position: relative;

    margin: -100px 0px 0px 150px; 
    padding-top: 100px;
    z-index: 1;
}

.summary {
    display: inline-block; 
    text-align: left; 
    margin-left: 10px;
    margin-top: 50px;
    z-index: 2;
}

.summAtt {
    margin-top: -10px;
}

.top {
    width: 100%;
    display: inline-flex;
    justify-content: left;
}

.more {
    width: 100%;
    display: inline-flex;
    justify-content: left;
}

.collapseDetails {
    text-align: left;
    width: 100%;
    display: flex;
    justify-content: left;
    margin-right: 35%;
    direction: rtl;
}

.detailBlurb {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.brighteDetailTitle, .lalehDetailTitle,
.lydiDetailTitle, .nakoaDetailTitle,
.rinDetailTitle, .robynDetailTitle, 
.suminaDetailTitle {
    font-size: 12px;
    margin-bottom: -13.25px;
}

.brighteDetailTitle:hover, 
.lalehDetailTitle:hover,
.lydiDetailTitle:hover, 
.nakoaDetailTitle:hover,
.rinDetailTitle:hover, 
.robynDetailTitle:hover, 
.suminaDetailTitle:hover {
    cursor: pointer;
    color: black;
}

.brighteBlurb, .lalehBlurb, .lydiBlurb,
.nakoaBlurb, .rinBlurb, .robynBlurb,
.suminaBlurb {
    font-size: 16px;
    max-height: 110px;
    margin-top: 0;
    overflow-x: hidden;
    overflow-y: auto;
    color: #939494;
    cursor: default;
    direction: ltr;
}

.brighteBlurb::-webkit-scrollbar,
.lalehBlurb::-webkit-scrollbar,
.lydiBlurb::-webkit-scrollbar,
.nakoaBlurb::-webkit-scrollbar,
.rinBlurb::-webkit-scrollbar,
.robynBlurb::-webkit-scrollbar,
.suminaBlurb::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 8px;
    left: 10px;
}

.brighteBlurb::-webkit-scrollbar-track,
.lalehBlurb::-webkit-scrollbar-track,
.lydiBlurb::-webkit-scrollbar-track,
.nakoaBlurb::-webkit-scrollbar-track,
.rinBlurb::-webkit-scrollbar-track,
.robynBlurb::-webkit-scrollbar-track,
.suminaBlurb::-webkit-scrollbar-track {
    border-right: 4px white solid;
    background-clip: padding-box;
}

.brighteBlurb::-webkit-scrollbar-track {
    background: #676aa6;
}

.lalehBlurb::-webkit-scrollbar-track {
    background: #a4814b;
}

.lydiBlurb::-webkit-scrollbar-track {
    background: #92c6e0;
}

.nakoaBlurb::-webkit-scrollbar-track {
    background: #b55038;
}

.rinBlurb::-webkit-scrollbar-track {
    background: #275435;
}

.robynBlurb::-webkit-scrollbar-track {
    background: #5b7086;
}

.suminaBlurb::-webkit-scrollbar-track {
    background: #cc3d4b;
}


.brighteBlurb::-webkit-scrollbar-thumb,
.lalehBlurb::-webkit-scrollbar-thumb,
.lydiBlurb::-webkit-scrollbar-thumb,
.nakoaBlurb::-webkit-scrollbar-thumb,
.rinBlurb::-webkit-scrollbar-thumb,
.robynBlurb::-webkit-scrollbar-thumb,
.suminaBlurb::-webkit-scrollbar-thumb {
    background-color: rgba(230, 230, 230, 0.5);
    -webkit-box-shadow: 0 0 4px rgba(255,255,255,.5);
    border-right: 4px white solid;
    background-clip: padding-box;
}

.brighteAttribute, .lalehAttribute,
.lydiAttribute, .nakoaAttribute,
.rinAttribute, .robynAttribute,
.suminaAttribute {
    font-weight: bold;
    display: inline-block;
    padding: 0px 3px 0px 3px;
    margin-bottom: -10px;
} 

.brighteAttribute {
    background-color: rgb(103, 106, 166, 0.4);
}

.lalehAttribute {
    background-color: rgb(164, 129, 75, 0.4);
}

.lydiAttribute {
    background-color: rgb(146, 198, 224, 0.4);
}

.nakoaAttribute {
    background-color: rgb(181, 80, 56, 0.4);
}

.rinAttribute {
    background-color: rgb(39, 84, 53, 0.4);
}

.robynAttribute {
    background-color: rgb(91, 112, 134, 0.4);
}

.suminaAttribute {
    background-color: rgb(204, 61, 75, 0.4);
}

.value {
    display: inline-block;
    margin-left: 10px;
    margin-bottom: -10px;
    color: #939494;
}