.nav {
  width: 55%;
  display: inline;
  padding: 30px;

  font-size: 120%;
  margin-left: 30%;
  float: right;
  text-align: right;
}

.link:hover span{
  font-weight: bold;
}

.link:hover{
  color: rgb(77, 77, 77);
}

.link {
  padding: 30px;
  color: #939494;
}

.active{
  color: black;
  font-weight: bold;
  text-decoration: underline !important;
  text-underline-offset: 10px;
  pointer-events: none;
}

