.kingpin_body {
    width: 100%;
    height: 100%;
    display: flex;
  
    font-size: 120%;
    font-family: Roboto, sans-serif;
    cursor: default;
}

.jokerIcon {
    width: 50px;
    height: 50px;
    margin: 20px;
    padding-left: 20px;
    top: 0;
    position: absolute;
}

.jokerIcon:hover {
    cursor: pointer;
}

.kingpin_details {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.kingpin_title {
    font-size: 350%;
    font-family: 'Playfair Display', serif;
}

.kingpin_excerpt {
    padding-left: 25%;
    padding-right: 25%;
    text-align: center;
    color: grey
}

.kingpin_selection {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-flow: wrap;
    justify-content: center;
    scroll-padding: 300px;
}

.selectInstruct {
    display: flex;
    justify-content: center;
    font-size: 14px;
    color: #939494;
    margin-top: 30px;
}

.greySection {
    position: relative;
    margin-top: -200px;
    z-index: -100;
    width: 100%;
    height: 150px;
    background-color: #d6d6d6;
}

.greyDetailsSection {
    position: absolute;
    margin-top: -282px;
    z-index: -100;
    width: 100%;
    height: 320px;
    background-color: #d6d6d6;
}

.detailsSection {
    display: inline;
}

.arrow, .upArrow {
    display: flex;

    width: 35px;
    height: 30px;

    margin-left: calc(50% - 17.5px);
}

.arrow {
    margin-top: 70px;
}

.upArrow {
    transform: rotate(180deg);
    cursor: pointer;
}

.sArrow_instruct {
    color: #939494;
    font-size: 14px;
    margin-top: 15px;
}

.character {
    width: 153.125px;
    height: 290px;
    margin: 7px;
    background-color: white;
    border-radius: 10px;
    border: 1px solid #d6d6d6;
}

.charaNames, .brighteActive, .lalehActive, 
.lydiActive, .nakoaActive, .rinActive,
.robynActive, .suminaActive {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    margin-top: 0px;
    margin-bottom: 0px;
    height: 30px;
    color: white;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.charaNames {
    background-color: grey;
}

.tagline {
    height: 42.5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 215px;

    color: grey;
    font-size: 14px;
    font-style: italic;
    line-height: 1;
    background-color: white;
    border-top: 1px solid black;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.card, 
.brighteCardIcon, .brighteCardInactive,
.lalehCardIcon, .lalehCardInactive,
.lydiCardIcon, .lydiCardInactive,
.nakoaCardIcon, .nakoaCardInactive,
.rinCardIcon, .rinCardInactive,
.robynCardIcon, .robynCardInactive,
.suminaCardIcon, .suminaCardInactive {
    width: 153.125px;
    height: 216.5625px;
    border-top: solid black 1px;
    border-bottom: solid black 1px;
    background: url('../components/kingpin_chara/kingpin_img/fullbody_temp.png') no-repeat;
    background-size: 153.125px 216.5625px;
    -moz-transition: scaleX(-1);
    -webkit-transition: scaleX(-1);
    -o-transition: scaleX(-1);
    -ms-transition: scaleX(-1);
    transition: scaleX(-1);
}

.brighteCardInactive, .lalehCardInactive,
.lydiCardInactive, .nakoaCardInactive,
.rinCardInactive, .robynCardInactive,
.suminaCardInactive {
    opacity: 0.5;
    filter: grayscale(100%); 
}

/*BRIGHTE -----------------------------------------------*/
.brighteCardIcon, .brighteCardInactive {
    background: url('../components/kingpin_chara/kingpin_img/brighte_icon.png') no-repeat;
    background-size: 153.125px 216.5625px;
    cursor: pointer;
}

.brighteActive {
    background-color: #676aa6;
